'use client';

import { useEffect, useState } from "react";
import Highcharts from "highcharts";
import HighchartsReact from "highcharts-react-official";
import { API_URL } from "../api/apiURL";

// Convert YYYY-MM-DD â†’ Su, Mo, Tu...
const getDayName = (dateStr) => {
  const days = ["Su", "Mo", "Tu", "We", "Th", "Fr", "Sa"];
  const d = new Date(dateStr);
  return days[d.getDay()];
};

// FORMATTER
const formatK = (num) => (num / 1000).toFixed(2) + " K";

export default function UserTrends() {
  const [trendData, setTrendData] = useState([]);
  const [funnelData, setFunnelData] = useState([]);

  // FETCH TREND DATA
  useEffect(() => {
    const loadTrend = async () => {
      try {
        const res = await fetch(`${API_URL}/admin/user-signup-trends`);
        const json = await res.json();

        if (json.success) {
          setTrendData(
            json.data.map((d) => ({
              day: getDayName(d.date), // ðŸ‘ˆ DAY FORMAT FIXED
              new: d.newUsers,
              returning: Math.floor(d.newUsers * 0.55),
            }))
          );
        }
      } catch (e) {
        console.log(e);
      }
    };
    loadTrend();
  }, []);

  // FETCH FUNNEL DATA
  useEffect(() => {
    const loadFunnel = async () => {
      try {
        const res = await fetch(`${API_URL}/admin/search-to-match`);
        const json = await res.json();
        if (json.success) setFunnelData(json.data);
      } catch (e) {
        console.log(e);
      }
    };
    loadFunnel();
  }, []);

  // ---------- HIGHCHART OPTIONS ----------
  const chartOptions = {
    chart: {
      type: "line",
      height: 330,
      spacingTop: 20,
      spacingLeft: 50,
      backgroundColor: "#ffffff",
    },

    title: {
      text: "Running User Trend",
      align: "left",
      margin: 30,
      style: {
        fontSize: "28px",
        fontWeight: "700",
      },
    },

    legend: {
      align: "right",
      verticalAlign: "top",
      layout: "horizontal",
      x: -20,
      y: 5,
      symbolRadius: 50,
      itemStyle: {
        fontSize: "16px",
        fontWeight: "500",
      },
    },

    xAxis: {
      categories: trendData.map((d) => d.day),
      lineWidth: 0,
      tickLength: 0,
      labels: {
        style: { fontSize: "18px", fontWeight: "500" },
      },
    },

    yAxis: {
      title: { text: null },
      gridLineColor: "#e5e7eb",
      tickPositions: [0, 5000000, 10000000, 15000000, 20000000, 25000000, 30000000],
      labels: {
        formatter: function () {
          return `${this.value / 1000000} M`;
        },
        style: { fontSize: "18px" },
      },
    },

    tooltip: { shared: true },

    series: [
      {
        name: "New Registered Users",
        data: trendData.map((d) => d.new),
        color: "#facc15",
        lineWidth: 4,
        marker: {
          radius: 9,
          lineWidth: 4,
          lineColor: "#facc15",
          fillColor: "#ffffff",
        },
      },
      {
        name: "Returning Users",
        data: trendData.map((d) => d.returning),
        color: "#16a34a",
        lineWidth: 4,
        marker: {
          radius: 9,
          lineWidth: 4,
          lineColor: "#16a34a",
          fillColor: "#ffffff",
        },
      },
    ],
  };

  return (
    <div className="mt-24 px-6 flex flex-col md:flex-row gap-10 w-full">

      {/* LEFT FUNNEL CARD */}
      <div className="bg-white rounded-2xl shadow-xl border border-gray-300 p-10 w-full md:w-1/2">
        <h2 className="text-3xl font-bold mb-10">Search To Match</h2>

        <div className="space-y-12">
          {funnelData.map((item, i) => {
            const maxVal = funnelData[0]?.value || 1;
            const percent = (item.value / maxVal) * 100;

            return (
              <div key={i} className="flex items-center justify-between">

                {/* LEFT LABEL */}
                <div className="w-[220px] text-[20px] font-medium text-gray-700">
                  {item.stage}
                </div>

                {/* BLUE BAR */}
                <div className="flex-1 mx-4">
                  <div className="w-full h-[28px] bg-gray-200 rounded-full shadow-inner overflow-hidden">
                    <div
                      className="h-full rounded-full"
                      style={{
                        width: percent + "%",
                        background: "linear-gradient(90deg,#3A47FF,#001BFF)",
                        boxShadow: "0px 3px 10px rgba(0,0,0,0.4)",
                      }}
                    ></div>
                  </div>
                </div>

                {/* VALUE */}
                <div className="w-[140px] text-right text-[20px] font-semibold text-gray-800">
                  {formatK(item.value)}
                </div>

              </div>
            );
          })}
        </div>
      </div>

      {/* RIGHT CHART CARD */}
      <div className="bg-white rounded-2xl shadow-xl border border-gray-300 p-10 w-full md:w-1/2">
        <HighchartsReact highcharts={Highcharts} options={chartOptions} />
      </div>
    </div>
  );
}
