"use client";

import dynamic from "next/dynamic";
import Highcharts from "highcharts";
import Highcharts3D from "highcharts/highcharts-3d";
import React, { useEffect, useState } from "react";
import { API_URL } from "../api/apiURL";

Highcharts3D(Highcharts);

const HighchartsReact = dynamic(() => import("highcharts-react-official"), {
  ssr: false,
});

export default function ProfileMatch() {
  const [pieData, setPieData] = useState([]);
  const [totalUsers, setTotalUsers] = useState(new Array(12).fill(0));
  const [matches, setMatches] = useState(new Array(12).fill(0));

  // ------------------ LOAD PROFILE OVERVIEW ------------------
  useEffect(() => {
    (async () => {
      try {
        const res = await fetch(API_URL + "/admin/profile-overview");
        const api = await res.json();
        if (!api.success) return;

        const colorMap = {
          Completed: "#0a8a1f",
          Moderate: "#f9cf36",
          Low: "#f79a20",
          Incomplete: "#de1e1e",
        };

        const order = ["Completed", "Moderate", "Low", "Incomplete"];

        const final = order.map((n) => {
          const found = api.data.find((x) => x.name === n);
          return {
            name: n,
            y: found ? found.value : 0,
            color: colorMap[n],
          };
        });

        setPieData(final);
      } catch (e) {
        console.log("Pie API error", e);
      }
    })();
  }, []);

  // ------------------ LOAD MATCHES DATA ------------------
  useEffect(() => {
    (async () => {
      try {
        const res = await fetch(API_URL + "/admin/matches-per-month");
        const api = await res.json();
        if (!api.success) return;

        const months = [
          "Jan","Feb","Mar","Apr","May","Jun",
          "Jul","Aug","Sep","Oct","Nov","Dec"
        ];

        setTotalUsers(
          months.map((m) => {
            const row = api.data.find((x) => x.month === m);
            return row ? row.totalUsers : 0;
          })
        );

        setMatches(
          months.map((m) => {
            const row = api.data.find((x) => x.month === m);
            return row ? row.matches : 0;
          })
        );
      } catch (e) {
        console.log("Match API error", e);
      }
    })();
  }, []);

  // ------------------ PIE CHART ------------------
  const pieOptions = {
    chart: {
      type: "pie",
      backgroundColor: "#ffffff",
      height: 350,
      options3d: { enabled: true, alpha: 55, beta: 0, depth: 55 },
    },
    title: {
      text: "Profile Overview",
      align: "left",
      style: { fontSize: "26px", fontWeight: 700 },
    },
    legend: {
      align: "right",
      verticalAlign: "top",
      itemStyle: {
        fontSize: "17px",
        fontWeight: 600,
      },
      symbolHeight: 22,
      symbolWidth: 22,
      symbolRadius: 4,
    },
    plotOptions: {
      pie: {
        depth: 45,
        allowPointSelect: true,
        borderWidth: 3,
        borderColor: "#fff",
        dataLabels: { enabled: false },
      },
    },
    series: [
      {
        type: "pie",
        data: pieData,
      },
    ],
    credits: { enabled: false },
  };

  // ------------------ MATCH PER MONTH CHART ------------------
  const matchOptions = {
    chart: {
      backgroundColor: "#ffffff",
      height: 420,
    },
    title: {
      text: "Matches Per Month",
      align: "left",
      style: { fontSize: "26px", fontWeight: 700 },
    },
    xAxis: {
      categories: [
        "Jan","Feb","Mar","Apr","May","Jun",
        "Jul","Aug","Sep","Oct","Nov","Dec"
      ],
      labels: { style: { fontSize: "15px", fontWeight: 600 } },
    },
    yAxis: {
      min: 0,
      max: 35,
      tickInterval: 5,
      labels: {
        formatter() { return this.value + " M"; },
      },
      title: { text: "" },
    },
    legend: {
      align: "right",
      verticalAlign: "top",
      itemStyle: { fontSize: "17px", fontWeight: 600 },
      symbolHeight: 18,
      symbolWidth: 18,
      symbolRadius: 4,
    },
    plotOptions: {
      column: {
        borderRadius: 18,
        pointPadding: 0.12,
        borderWidth: 0,
        shadow: true,
      },
      line: {
        shadow: true,
      },
    },
    series: [
      {
        type: "column",
        name: "Total no. of Users",
        color: {
          linearGradient: { x1: 0, y1: 0, x2: 0, y2: 1 },
          stops: [
            [0, "#f8d74d"],
            [1, "#e0af05"],
          ],
        },
        data: totalUsers,
      },
      {
        type: "line",
        name: "No. Of Matches",
        color: "#e22b2b",
        lineWidth: 4,
        marker: {
          enabled: true,
          radius: 7,
          fillColor: "#fff",
          lineColor: "#e22b2b",
          lineWidth: 2,
        },
        data: matches,
      },
    ],
    credits: { enabled: false },
  };

  return (
    <div className="w-full p-4">
      <div className="grid grid-cols-1 xl:grid-cols-2 gap-6">

        <div className="bg-white shadow-2xl rounded-2xl p-6">
          <HighchartsReact highcharts={Highcharts} options={pieOptions} />
        </div>

        <div className="bg-white shadow-2xl rounded-2xl p-6">
          <HighchartsReact highcharts={Highcharts} options={matchOptions} />
        </div>

      </div>
    </div>
  );
}
